<template>
  <div class="hello-world">
    <User :name="'ololo'" />
  </div>
</template>

<script>
  import User from './User';
  export default {
    name: 'HelloWorld',
    components: { User },
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }
</style>



