<template>
  <div class="user">
    <h3>{{ msg }}</h3>
    <p>{{ name }}</p>
  </div>
</template>

<script>
  export default {
    name: 'User',
    props: {
      name: {
        type: String,
        required: true,
        default: 'default not set',
      },
    },
    data() {
      return {
        msg: 'a msg',
      };
    },
    methods: {
      upper() {
        return this.name.toUpperCase();
      },
    },
  };
</script>



